<template>
  <el-dialog v-model="dialogVisible" title="使用说明" center>
    <p>
      1. 从钉钉上下载模板文件<br />
      2. 上传钉钉模板文件和学生成绩文件<br />
      3. 点击下载文件<br />
      4. 将下载的文件上传到钉钉成绩录入。<br />
      5. 文件在本地处理，不会上传到服务器。<br />
      6. 模板根据输入的文件自动识别，支持以下格式
    </p>
    <el-scrollbar height="200px">
      <el-space direction="vertical">
        <el-card v-for="mymode in modeList" :key="mymode" shadow="never">
          <template #header>
            <span>{{
              `表格名称: ${mymode.sheetName} 班级位置: ${mymode.classColumn}${mymode.base}`
            }}</span>
          </template>

          映射关系:
          <el-space wrap>
            <el-tag v-for="(name, to) in mymode.columnMap" :key="name">{{
              `${name}(${to})`
            }}</el-tag>
          </el-space>
        </el-card>
      </el-space>
    </el-scrollbar>
    <a href="https://github.com/HGGshiwo/scoremanager">项目地址</a>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >确定</el-button
        >
      </span>
    </template>
  </el-dialog>
  <el-row style="marging-bottom: 20px">
    <el-col :span="4">
      <el-form-item label="成绩输入模式">
        <el-tag effect="plain" size="large">{{
          mode === 0 ? "期中" : mode === -1 ? "待检测" : "期末"
        }}</el-tag>
      </el-form-item>
    </el-col>
    <el-col :span="3" :push="15">
      <el-button type="text" @click="dialogVisible = true">使用说明</el-button>
    </el-col>
  </el-row>

  <el-upload
    ref="upload"
    v-for="(text, i) in ['请上传钉钉模板', '请上传成绩文件']"
    :key="text"
    :limit="1"
    drag
    class="avatar-uploader"
    :on-change="
      (file) => {
        handleChange(file, i);
      }
    "
    :on-remove="
      (file) => {
        handleRemove(file, i);
      }
    "
    :on-exceed="
      (files) => {
        handleExceed(files[0], i);
      }
    "
    style="width: 80%; margin-left: 10%; margin-bottom: 10px"
    :auto-upload="false"
  >
    <!-- <el-icon class="avatar-uploader-icon"><Plus /></el-icon> -->
    <el-icon class="avatar-uploader-icon"
      ><Check v-if="uploadState[i].ok" /><Plus v-else
    /></el-icon>
    <template #tip>
      <div class="el-upload__tip">
        {{ uploadState[i].ok ? uploadState[i].name : text }}
      </div>
    </template>
  </el-upload>
  <el-button :loading="loading" type="primary" @click="handleClick"
    >下载文件</el-button
  >
</template>

<script>
import { Plus, Check } from "@element-plus/icons-vue";
import { ref } from "@vue/reactivity";
import { read, write, utils, readFile } from "xlsx"; // 注意处理方法引入方式
import { ElMessageBox, genFileId } from "element-plus";

// 将一个sheet转成最终的excel文件的blob对象，然后利用URL.createObjectURL下载
function sheet2blob(sheet, sheetName) {
  sheetName = sheetName || "sheet1";
  var workbook = {
    SheetNames: [sheetName],
    Sheets: {},
  };
  workbook.Sheets[sheetName] = sheet;
  // 生成excel的配置项
  var wopts = {
    bookType: "xlsx", // 要生成的文件类型
    bookSST: false, // 是否生成Shared String Table，官方解释是，如果开启生成速度会下降，但在低版本IOS设备上有更好的兼容性
    type: "binary",
  };
  var wbout = write(workbook, wopts);
  var blob = new Blob([s2ab(wbout)], { type: "application/octet-stream" });
  // 字符串转ArrayBuffer
  function s2ab(s) {
    var buf = new ArrayBuffer(s.length);
    var view = new Uint8Array(buf);
    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
    return buf;
  }
  return blob;
}
/**
 * 通用的打开下载对话框方法，没有测试过具体兼容性
 * @param url 下载地址，也可以是一个blob对象，必选
 * @param saveName 保存文件名，可选
 */
function openDownloadDialog(url, saveName) {
  if (typeof url == "object" && url instanceof Blob) {
    url = URL.createObjectURL(url); // 创建blob地址
  }
  var aLink = document.createElement("a");
  aLink.href = url;
  aLink.download = saveName || ""; // HTML5新增的属性，指定保存文件名，可以不要后缀，注意，file:///模式下不会生效
  var event;
  if (window.MouseEvent) event = new MouseEvent("click");
  else {
    event = document.createEvent("MouseEvents");
    event.initMouseEvent(
      "click",
      true,
      false,
      window,
      0,
      0,
      0,
      0,
      0,
      false,
      false,
      false,
      false,
      0,
      null
    );
  }
  aLink.dispatchEvent(event);
}

export default {
  components: {
    Plus,
    Check,
  },
  setup() {
    const dialogVisible = ref(false);
    const mode = ref(-1);
    const upload = ref();
    const activePage = ref(1);
    const loading = ref(false);
    let absentStu = [];
    const uploadState = ref([
      { name: "", ok: false, file: undefined },
      { name: "", ok: false, file: undefined },
    ]);

    let totalNameList = [];

    const handleChange = (file, i) => {
      if (checkType(file.raw) === false) {
        upload.value[i].clearFiles();
        return;
      }
      uploadState.value[i].name = file.name;
      uploadState.value[i].ok = true;
      handleFile(file, i);
    };

    const readFile2 = (workbook) => {
      //读取成绩文件
      try {
        //自动设置模式
        mode.value = -1;
        for (var j = 0; j < modeList.value.length; j++) {
          if (workbook.SheetNames.includes(modeList.value[j].sheetName)) {
            mode.value = j;
            console.log(modeList.value[j].sheetName, mode.value);
          }
        }
        if (mode.value === -1) throw Error("文件匹配失败");
        uploadState.value[1].file =
          workbook.Sheets[modeList.value[mode.value].sheetName];
      } catch (e) {
        upload.value[1].clearFiles();
        ElMessageBox.alert(`读取成绩文件错误: ${e}`);
      }
    };

    const readFile1 = (workbook) => {
      try {
        uploadState.value[0].file = workbook.Sheets[workbook.SheetNames[0]];
      } catch (e) {
        upload.value[0].clearFiles();
        ElMessageBox.alert(`读取模板文件错误: ${e}`);
      }
    };

    const handleFile = (file, i) => {
      if (file.raw) file = file.raw;
      const fileReader = new FileReader();
      fileReader.onload = (ev) => {
        loading.value = true;
        const data = ev.target.result;
        const workbook = read(data, { type: "binary" });
        i == 0 ? readFile1(workbook) : readFile2(workbook);
        loading.value = false;
      };
      fileReader.readAsBinaryString(file);
    };

    const handleRemove = (file, i) => {
      uploadState.value[i].ok = false;
      uploadState.value[i].file = undefined;
      if (i === 1) {
        mode.value = -1;
      }
    };

    const modeList = ref([
      {
        columnMap: {
          学号: "A",
          语文: "F",
          数学: "G",
          英语: "H",
          物理: "I",
          化学: "J",
          生物: "N",
          政治: "K",
          历史: "L",
          地理: "M",
          技术: "O",
          外语: "",
          年级总分排名: "S",
          语文年级排名: "",
          数学年级排名: "",
          英语年级排名: "",
          物理年级排名: "",
          化学年级排名: "",
          生物年级排名: "",
          政治年级排名: "",
          历史年级排名: "",
          地理年级排名: "",
          技术年级排名: "",
          外语年级排名: "",
        },
        sheetName: "年段",
        base: 3,
        classColumn: "B",
      },
      {
        columnMap: {
          学号: "A",
          语文: "F",
          数学: "J",
          英语: "N",
          物理: "R",
          化学: "V",
          生物: "Z",
          政治: "AD",
          历史: "AH",
          地理: "AL",
          技术: "AP",
          外语: "AT",
          年级总分排名: "CR",
          语文年级排名: "H",
          数学年级排名: "L",
          英语年级排名: "P",
          物理年级排名: "T",
          化学年级排名: "X",
          生物年级排名: "AB",
          政治年级排名: "AF",
          历史年级排名: "AJ",
          地理年级排名: "AN",
          技术年级排名: "AR",
          外语年级排名: "AV",
        },
        sheetName: "学生成绩",
        base: 4,
        classColumn: "D",
      },
    ]);

    const handleExceed = (file, i) => {
      if (!checkType(file)) {
        return;
      }
      upload.value[i].clearFiles();
      file.uid = genFileId();
      upload.value[i].handleStart(file);
      uploadState.value[i].name = file.name;
      handleFile(file, i);
    };

    const handleClick = () => {
      if (!uploadState.value[0].ok) {
        ElMessageBox.alert("未输入钉钉模板");
        return;
      }
      if (!uploadState.value[1].ok) {
        ElMessageBox.alert("未输入学生成绩文件");
        return;
      }
      let classNo = -1;
      let columnList = [];
      let nameList = [];
      try {
        absentStu = [];
        loading.value = true;
        classNo = uploadState.value[0].file["B4"].v.replace(/[^0-9]+/g, "");
        columnList = Object.keys(uploadState.value[0].file)
          .filter((key) => {
            return key.match(/^[A-Z]+3$/);
          })
          .map((key) => {
            const columnMap = modeList.value[mode.value].columnMap;
            return {
              raw: key.replace(/[0-9]+/g, ""),
              to: columnMap[uploadState.value[0].file[key].v],
            };
          });

        nameList = Object.keys(uploadState.value[0].file)
          .filter((key) => {
            if (key === "C3") return false;
            return key.match(/C[0-9]+/);
          })
          .map((key) => {
            return uploadState.value[0].file[key].v;
          });
      } catch (e) {
        loading.value = false;
        ElMessageBox.alert(`钉钉模板文件解析错误: ${e}`);
        return;
      }

      try {
        totalNameList = Object.keys(uploadState.value[1].file)
          .filter((key) => {
            for (var i = 0; i < modeList.value[mode.value].base; i++) {
              if (key === `C${i}`) return false;
            }
            return key.match(/^C[0-9]+$/);
          })
          .map((key) => {
            return uploadState.value[1].file[key].v;
          });
      } catch (e) {
        loading.value = false;
        ElMessageBox.alert(`成绩文件解析错误: ${e}`);
      }

      try {
        new Promise((resolve, reject) => {
          nameList.forEach((name, i) => {
            let row = find(classNo, name);
            if (row == -1) {
              absentStu.push(name);
              return;
            }
            columnList.forEach((column) => {
              if (["B", "C", "O"].includes(column.raw)) return; //模板中BCO不填
              if (column.to === "") return; //如果没有映射不填
              if (row == -1) return; //学生没有参加考试
              let cell = uploadState.value[1].file[`${column.to}${row}`];
              if (cell) {
                //学生参加该科目考试
                uploadState.value[0].file[`${column.raw}${i + 4}`] = {
                  v: cell.v,
                  t: cell.t,
                  w: cell.w,
                };
              }
            });
          });
          resolve();
        }).then(() => {
          loading.value = false;
          let name = uploadState.value[0].name.split(".");

          ElMessageBox.alert(`${absentStu}未参加考试.`, "提示", {
            confirmButtonText: "好的",
            callback: () => {
              openDownloadDialog(
                sheet2blob(uploadState.value[0].file, "成绩单模板1"),
                `${name[0]}-生成.${name[1]}`
              );
            },
          });
        });
      } catch (e) {
        loading.value = false;
        ElMessageBox.alert(`表格填写错误: ${e}`);
        return;
      }
    };

    const find = (classNo, name) => {
      for (var i = 0; i < totalNameList.length; i++) {
        if (totalNameList[i] !== name) {
          continue;
        }
        let row = i + modeList.value[mode.value].base;
        let classColumn = modeList.value[mode.value].classColumn;
        if (classNo == uploadState.value[1].file[`${classColumn}${row}`].v) {
          return row;
        }
      }
      return -1;
    };

    const checkType = (rawFile) => {
      if (rawFile.type !== "application/vnd.ms-excel") {
        ElMessageBox.alert("必须是excel类型的文件");
        return false;
      }
      return true;
    };

    // const openInst = () => {
    //   ElMessageBox({
    //     title: "使用说明",
    //     dangerouslyUseHTMLString: true,
    //     message: `
    //       <div>1. 从钉钉上下载模板文件<div>
    //       <div>2. 上传钉钉模板文件和学生成绩文件<div>
    //       <div>3. 点击下载文件</div>
    //       <div>4. 将下载的文件上传到钉钉成绩录入。</div>
    //      <a href="https://github.com/HGGshiwo/scoremanager">项目地址</a>`,
    //   });
    // };

    return {
      activePage,
      handleChange,
      handleRemove,
      handleExceed,
      uploadState,
      handleClick,
      loading,
      upload,
      mode,
      modeList,
      dialogVisible,
    };
  },
};
</script>


<style scoped>
.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>